.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding: 1rem 2rem;
  border-bottom: 1px solid #e0e0e0;
}

.main-nav-logo img {
  max-height: 40px;
}

.main-nav-items {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.main-nav-items .main-nav-user {
  font-weight: 600;
  color: #00bc77;
  font-size: 1rem;
}
.main-nav-items .main-nav-user i {
  margin-right: 0.5rem;
}
.main-nav-items .icon {
  font-size: 1.3rem;
  color: #00bc77;
  cursor: pointer;
  transition: color 0.2s ease;
}
.main-nav-items .icon:hover {
  color: #00965d;
}
.main-nav-items .logout:hover {
  color: #e44646;
}